import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './plugins/axios'
import './plugins/element.js'
//引用字体图标
import './assets/iconfont/iconfont.css'
import './assets/iconfont/iconfont.js'
//全局引用sass
import './sass/style.scss'
// 全局引用 幻灯片插件
import VueAwesomeSwiper from 'vue-awesome-swiper'
// require styles
import 'swiper/dist/css/swiper.css'
Vue.use(VueAwesomeSwiper, /* { default global options } */)

//使用 Mint-UI 组件   npm i mint-ui
// 第一步： 导入Mint-UI
//第二步 导入mint-ui 的样式  包在mode_modules 里面  路径省略了
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
//第三步：  手动安装一下
Vue.use(MintUI)//把所以组件 注册为全局组件

//引入vant
import Vant from 'vant'
import 'vant/lib/index.css';
//全部注册
Vue.use(Vant);
//机器人

import BotUI from 'botui'
// import 'botui.min.css';
// import 'botui-theme-default.css';
// const botui = BotUI('my-botui-app', {
//   vue: Vue // pass the dependency.
// })

//百度地图
import BaiduMap from 'vue-baidu-map'
Vue.use(BaiduMap, {
  ak: 'gyQnwN7Gw2gFs6RQ0gmGm7YDGlBN5mvb' //去百度地图申请
})
//导入语音播报
import * as voicePromptFun from './func/voicePrompt' 
Vue.prototype.voicePrompt = voicePromptFun.voicePrompt  //语音提醒
//图片剪切  未用到
import VueCropper from 'vue-cropper' 
Vue.use(VueCropper)

// 图片懒加载
import VueLazyload from 'vue-lazyload'
Vue.use(VueLazyload)
// or with options
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: 'dist/error.png',
  loading: 'dist/loading.gif',
  attempt: 1,
  listenEvents: [ 'scroll' ]
})


import http from './http.js'
 
//axios不支持Vue.use  可以添加原型使用
Vue.prototype.$http = http

Vue.config.productionTip = false
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
