</template>

<script>
import { Toast } from 'mint-ui'
import userLike from "../../components/home/userLike";
import navFooter from "../../components/public/navFooter";
  export default {

    components: {
      userLike,
      navFooter
    },
    data(){
      return {
      	login: false,
      	user: {},
        url: '',
        orderCat: ['待付款','待发货','待收货','待评价'],
        swiperOption: {
                  spaceBetween: 30,
                  centeredSlides: true,
                  autoplay: {
                    delay: 2500,
                    disableOnInteraction: false
                },
                pagination: {
                	el: '.swiper-pagination',
                	type: 'fraction'
                },
            },
            orderList: [],
            hasOrder: false
      }
    },
    methods:{
      async getAllOreder(){
      	this.$store.commit('showLoading')
      	 const res = await this.$http.get(`order/findOrderByStatus.do?`)
      	 if (res.data.code != 0) {
      	 	this.hasOrder = true
      	 	this.$store.commit('hideLoading')
      	 	res.data.forEach((item,i) => {
      	 		if (item.order.orderStatus != '5') {
      	 			this.orderList.push(item)
      	 		}
      	 		
      	 	})
      	 	
      	 }
      	 if (res.data.message == '订单不存在') {
      	 	 this.hasOrder = false
             this.$store.commit('hideLoading')
      	 }
      },
      sign(){
      	if (this.login) {
      		Toast('签到成功 经验值加 +1')
      	}else{
      		this.$router.push('/login')
      	}
      	

      }
    },
    created(){

    	
    	this.login = this.$store.getters.getToken ? true : false;
    	this.user = this.$store.getters.userInfo; 
    	if (this.login) {
    		this.getAllOreder();
    	}
    	
    }
};
</script>

