<template>
    <div id="container">
        <mt-header class="top" title="医生小助手" style="background:#fff;border-bottom:1px solid #bbb;height:6vh;color:#101010">  
            <mt-button icon="back" slot="left" @click="btn1()" style="display:inline-block;text-indent:5px;"></mt-button>
        </mt-header>

        <ul :class="[!faceShow?'noShow':'isShow','content']"  ref="charView">
            <li v-for="(item, index) in messageList" :key="index">
                <img v-if="!item.myself" src="../../assets/images/doctor.jpg" class="imgleft">
                <img v-if="item.myself" :src="user.haedPic" class="imgright">
                <span v-html="item.message" contenteditable="true" :class="'span'+(item.myself?'right':'left')">

                
                </span>
                
            </li>
        </ul>
        <div class="footer_wrap">
            <div class="px-2 py-2">
                
                <!-- <div v-if="faceShow" @click="" class="iconfont icon-xinaixin fs-xxl"></div> -->
            </div>
            
            <div class="footer">
                
                   <input id="text" type="text" v-model.trim="inputValue" @keyup.enter='chat' placeholder="" ref="shareTextArea"> 
                
                
                <div @click="changShow" :class="['iconfont',faceShow?'icon-dingdan':'icon-xinaixin','fs-xxxl','emojo']"></div>
                
                    <span id="btn" @click="btn2()">发送</span>
                
                

            </div>
            <!-- //表情区域 -->
            <emotion @emotion="handleEmotion" :height="200" v-if="faceShow"></emotion>
        </div>

        
        
    </div>
</template>

<script>
    import Emotion from '@/components/public/emotion'

    export default{

        name:'Kefu',
        data() {
            return {
             //输入的内容,事先约定好的
             inputValue: '',
            //聊天的数组内容
            messageList: [
            {
                message: '小医为您服务~',
                myself: false
            },
            {
                message: '请问你那里不舒服？',
                myself: false
            },
            ],
            scrollHeight: 0,
            user:{},
            
            faceShow: false,
            content: '',
            comment: '',

        }
    },//点击回车时候发送信息
    components: {
      Emotion
    },
    updated:function(){
        this.scrollToBottom();
    },
    created(){
        this.user = this.$store.getters.userInfo; 
    },
    methods: {
        // 是否显示表情框
        changShow(){
            this.faceShow = !this.faceShow
        },
        scrollToBottom() {
         this.$nextTick(() => {
            var container =this.$refs.charView;

            container.scrollTop = container.scrollHeight;
        })},

         chat() {
            this.messageList.push({
                message: this.inputValue.replace(/\#[\u4E00-\u9FA5]{1,3}\;/gi, this.emotion),
                    //这个是判断当前是否是自己输入的内容,自己的是true,机器人的是false
                    myself: true
                })
                //每次发送信息之后机器人就要回复,所以添加完之后直接调用机器人的接口
                this.$http.post('user/chatBot.do',{
                    text: this.inputValue,
                }).then(res => {
                    console.log(res)
                    this.messageList.push({

                        message: res.data.results[0].values.text,
                        myself: false
                    });
                    this.$refs.charView.scrollTop=1000000;
                    this.scrollToBottom()

                })

                this.inputValue=""
                
            },
            btn1(){
                this.$router.go(-1)
            },
            btn2(){

                this.messageList.push({
                    message: this.inputValue.replace(/\#[\u4E00-\u9FA5]{1,3}\;/gi, this.emotion),
                    //这个是判断当前是否是自己输入的内容,自己的是true,机器人的是false
                    myself: true
                })
                this.$http.post('user/chatBot.do',{
                    text: this.inputValue,
                    
                }).then(res => {

                    this.messageList.push({
                        message: res.data.results[0].values.text,
                        myself: false
                    });

                })

                this.inputValue=""
                
            },
            handleEmotion (i) {
              this.inputValue += i
          },
    // 将匹配结果替换表情图片
    emotion (res) {
      let word = res.replace(/\#|\;/gi,'')
      const list = ['微笑', '撇嘴', '色', '发呆', '得意', '流泪', '害羞', '闭嘴', '睡', '大哭', '尴尬', '发怒', '调皮', '呲牙', '惊讶', '难过', '酷', '冷汗', '抓狂', '吐', '偷笑', '可爱', '白眼', '傲慢', '饥饿', '困', '惊恐', '流汗', '憨笑', '大兵', '奋斗', '咒骂', '疑问', '嘘', '晕', '折磨', '衰', '骷髅', '敲打', '再见', '擦汗', '抠鼻', '鼓掌', '糗大了', '坏笑', '左哼哼', '右哼哼', '哈欠', '鄙视', '委屈', '快哭了', '阴险', '亲亲', '吓', '可怜', '菜刀', '西瓜', '啤酒', '篮球', '乒乓', '咖啡', '饭', '猪头', '玫瑰', '凋谢', '示爱', '爱心', '心碎', '蛋糕', '闪电', '炸弹', '刀', '足球', '瓢虫', '便便', '月亮', '太阳', '礼物', '拥抱', '强', '弱', '握手', '胜利', '抱拳', '勾引', '拳头', '差劲', '爱你', 'NO', 'OK', '爱情', '飞吻', '跳跳', '发抖', '怄火', '转圈', '磕头', '回头', '跳绳', '挥手', '激动', '街舞', '献吻', '左太极', '右太极']
      let index = list.indexOf(word)
      return `<img src="https://res.wx.qq.com/mpres/htmledition/images/icon/emotion/${index}.gif" align="middle">`   
  }



},
mounted() {
 this.box=this.$refs.charView;
 this.scrollToBottom();
},
}
</script>
<style lang="scss" scoped>


* {    
    margin: 0;
    padding: 0;
    list-style: none;
    font-family: '微软雅黑'
}
.top{
    width: 100%;
    position: fixed;
    top: 0;
}
#container {
    width: 100vw;
    height: 100vh;
    background: #fafafa;
    position: relative;
    box-shadow: 20px 20px 55px #777;
}
.header {

    background: #000;
    height: 40px;
    color: #fff;
    line-height: 34px;
    font-size: 20px;
    padding: 0 10px;
}
.footer_wrap {
    width: 100vw;
    /*height: 4.2rem;*/

    position: fixed;
    bottom: 0
}
.footer {
    width: 100vw;
    height: 4.2rem;
    background-color: #edeff0;

    overflow: hidden;

}
.footer input {
    margin-top: 10px;
    width: 65vw;
    height: 40px;
    outline: none;
    font-size: 20px;
    text-indent: 10px;
    position: absolute;
    border-radius: 6px;
    margin-left: 10px;
}
.emojo {
    margin-top: 10px;
    width: 10vw;
    height: 40px;
    position: absolute;
    display: inline-block;
    right: 20%;
    text-align: center;
    line-height: 40px;
}
.footer span {
    margin-top: 10px;
    display: inline-block;
    width: 15vw;
    height: 40px;
    background: #ccc;
    font-weight: 900;
    line-height: 40px;
    cursor: pointer;
    text-align: center;
    position: absolute;
    right: 10px;
    border-radius: 6px;
}
.footer span:hover {
    color: #fff;
    background: #999;
}
#user_face_icon {
    display: inline-block;
    background: red;
    width: 60px;
    height: 60px;
    border-radius: 30px;
    position: absolute;
    bottom: 6px;
    left: 14px;
    cursor: pointer;
    overflow: hidden;
}
img {
    width: 50px;
    height: 50px;
}
.content {
    font-size: 20px;
    width: 100vw;
    height: 100vh;
    padding-top: 50px;
    
    overflow: auto;

}
.isShow {
  padding-bottom: 280px;
}
.noShow {
    padding-bottom: 80px;
}
.content li {
    margin-top: 10px;
    display: block;
    clear: both;
    overflow: hidden;
}
.imgleft {
    float: left;
}
.imgright {
    float: right;
}
.content li span{
    background: #7cfc00;
    padding: 10px;
    border-radius: 10px;
    float: left;
    margin: 6px 10px 0 10px;
    max-width: 310px;
    border: 1px solid #ccc;
    box-shadow: 0 0 3px #ccc;
    word-break: break-all;

}

.content li span.spanleft { 
    float: left;
    background: #fff;
    line-height: 1.4rem;
    display: flex;
    align-items: center;
}
.content li span.spanright { 
    float: right;
    background: #7cfc00;
    line-height: 1.4rem;
    display: flex;
    align-items: center;
}
/*表情区域*/
.browBox {
 width: 100%;
 height: 200px;
 background: #e6e6e6;
 

 overflow: scroll;
 ul {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  li {
  width: 14%;
  font-size: 26px;
  list-style: none;
  text-align: center;
  padding-bottom: 15px;
  color: #ffc32d;
  }
 }
 }
</style>
